import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NavController } from '@ionic/angular';
import { GetDataBackendService } from '../../service/get-data-backend.service';
import { LocalStorageServiceService } from '../../service/local-storage-service.service';

@Component({
  selector: 'app-registro',
  templateUrl: './registro.page.html',
  styleUrls: ['./registro.page.scss'],
})
export class RegistroPage implements OnInit {

  TipoClientes: any = ['Cliente', 'Barbero']
  ListBarberos: any = []
  ShowPreguntas: Boolean = false
  ShowPass: Boolean = false;

  RegistroForm: FormGroup;
  constructor(
    public navCtrl: NavController,
    private _GetDatos: GetDataBackendService,
    private _localstorage: LocalStorageServiceService,
    public formBuilder: FormBuilder
  ) {
    this.RegistroForm = this.formBuilder.group({
      idUser: Math.random().toString(36).substring(2, 8 + 2),
      TipoUser: ['Seleccione'],
      Name: ['', [Validators.required]],
      Phone: [''],
      Email: ['', [Validators.required, Validators.email, Validators.minLength(2)]],
      Password: ['', [Validators.required, Validators.minLength(6)]],
      Barberia: [''],
      idBarbero: [''],
      Barbero: [''],
      PreguntaUno: ['', [Validators.required, Validators.minLength(8)]],
      RespuestaUno: ['', [Validators.required, Validators.minLength(3)]],
      PreguntaDos: ['', [Validators.required, Validators.minLength(8)]],
      RespuestaDos: ['', [Validators.required, Validators.minLength(3)]],
    })
  }

  ngOnInit() {
    this.GetListBarber()
  }
  get Datosregistro() { return this.RegistroForm.value }

  OptionPass() {
    //Mostrar u ocultar clave
    switch (this.ShowPass) {
      case true:
        this.ShowPass = false
        break;

      case false:
        this.ShowPass = true
        break;
    }
  }

  GetListBarber() {
    //busco la lista de todos los barberos 
    try {

      this._GetDatos.GetTipoDeUsusarios('Barbero').then((Res: any) => {

        if (Res.length > 0) {
          //si encontre data se la asigno mi lista de barberos 
          this.ListBarberos = Res
        }

      }).catch(error => console.error(error))

    } catch (error) {
      console.error(error)
    }
  }

  GuardarDatosRegistro() {

    if (this.Datosregistro.Barbero) {
      //verifico que el usurio haya seleccionado un barbero 

      //busco los datos del cliente seleccionado 
      const BarberoSelect = this.ListBarberos.find((B: any) => B.Name === this.Datosregistro.Barbero)

      //asigno el id del barbero seleccioando 
      this.Datosregistro.idBarbero = BarberoSelect.idUser

    }

    // registro los datos del usuairo 
    this._GetDatos.InsertUser(this.Datosregistro).then((Res: any) => {
      if (Res.status === true) {
        this._localstorage.GuardarItem('DatosUser', JSON.stringify(this.Datosregistro))
        this.navCtrl.navigateForward("/home");
      }
    }).catch(error => console.error(error))

  }
}
